.copyright {
    padding: 30px 0;
    text-align: center;

    p {
        font-size: 12px;
        font-weight: 500;
        color: #8a8c90;
        line-height: 1.4;
        margin: 0;
    }
}

@media (max-width: 450px) {
    .copyright {
        padding: 20px 0;
    }

    .copyright p {
        width: 65%;
        margin: 0 auto;
    }
}
