.cart__wrap {
    margin-top: 50px;
}

.cart__title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 22px;
}

.cart__item {
    margin-bottom: 40px;
    display: flex;
    min-height: 150px;
    max-height: 150px;

    .product__bg {
        min-width: 250px;
        max-width: 250px;
    }
}

.product__right {
    display: flex;
    background-color: #f6f6f6;
    width: 100%;
    padding: 16px 50px 0 30px;
}

.product__actions {
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-top: 12px;

    div {
        margin: 0 100px 35px;

        h5 {
            color: #8a8c90;
            font-weight: 500;
            text-align: right;
            margin-bottom: 18px;
        }

        p {
            font-size: 14px;
            font-weight: 600;
        }
    }

    p span {
        cursor: pointer;
        font-size: 19px;
        width: 10px;
        height: 10px;
        padding: 0 7px;
        margin: 5px;
        border-radius: 2px;
        color: $black;
        background-color: $white;
        text-align: center;
        transition-duration: 300ms;
        transition-property: background-color;

        &:first-child {
            padding: 0 8px;
        }

        &:hover {
            background-color: darken($white, 15%);
        }
    }
}

.product__info {
    div > span {
        font-size: 14px;
        border-radius: 4px;
        padding: 0 8px;
        display: inline-block;
        background: #f13737;
        color: #fff;
        margin-bottom: 8px;
        font-weight: 500;
    }

    h4 {
        font-size: 15px;
        font-weight: 600;
        margin-bottom: 18px;
    }

    p {
        font-size: 12px;
        font-weight: 600;

        span {
            color: #2ecc71;
        }
    }
}

.cart__total {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 60px;
}

.cart__total-top {
    background-color: #f6f6f6;
    padding: 18px 40px;
    font-weight: 500;
    margin-bottom: 15px;

    p {
        display: inline-block;
        margin-right: 95px;
        margin-bottom: 0;
    }

    span {
        font-weight: 600;
    }
}

.cart__total-bottom {
    .cart__link {
        font-size: 12px;
        color: #8a8c90;
        margin-right: 60px;
        transition-duration: 300ms;
        transition-property: color;

        &:hover {
            color: $black;
        }
    }

    .cart__btn {
        display: inline-block;
        text-decoration: none;
        color: $white;
        background-color: #ef8767;
        font-weight: 500;
        max-width: 140px;
        border-radius: 4px;
        padding: 14px 30px;
        line-height: 1;
        transition-duration: 300ms;
        transition-property: background-color;

        &:hover {
            background-color: darken(#ef8767, 10%);
        }
    }
}

.fa-times {
    cursor: pointer;
    color: #8a8c90;
    margin-bottom: 14px;
    transition-duration: 300ms;
    transition-property: color;

    &:hover {
        color: $black;
    }
}

@media (max-width: 992px) {
    .product__actions div {
        margin: 0 50px 35px;
    }
}

@media (max-width: 835px) {
    .cart__item {
        flex-direction: column;
        margin-bottom: 155px;
    }
}

@media (max-width: 630px) {
    .product__right {
        padding: 16px 18px 0 18px;
    }
}

@media (max-width: 575px) {
    .product__actions div {
        margin: 0 20px 35px;
    }

    .cart__wrap {
        margin-top: 0;
    }
}

@media (max-width: 450px) {
    .cart__item {
        margin-bottom: 230px;
    }

    .cart__total {
        align-items: flex-start;
    }

    .cart__total-top {
        width: 100%;
        padding: 18px 20px;
        margin-bottom: 20px;
        display: flex;

        p {
            margin-right: auto;
        }
    }

    .cart__total-bottom {
        width: 100%;
        display: flex;
        align-items: center;

        .cart__btn {
            padding: 14px 25px;
        }

        .cart__link {
            margin-right: auto;
        }
    }

    .product__right {
        flex-direction: column;
        padding: 16px 0 0 0;
        max-height: 195px;
    }

    .product__info div {
        text-align: center;
    }

    .product__actions {
        margin: 12px auto 0;
    }

    .product__actions div h5 {
        transform: translateY(10px);
    }
}
