/* Customize the label (the container) */
.label-container {
    display: block;
    position: relative;
    padding-left: 33px;
    margin-bottom: 8px;
    cursor: pointer;
    font-size: 12px;
    font-weight: 500;
    color: #8a8c90;
    user-select: none;
    line-height: 1.5;
}

/* Hide the browser's default radio button */
.label-container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom radio button */
.radio-checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 18px;
    width: 18px;
    background-color: #e9ecef;
    border-radius: 50%;
    border: 2px solid #e9ecef;
}

/* On mouse-over, add a grey background color */
// .label-container:hover input ~ .radio-checkmark {
//     background-color: #ccc;
// }

/* When the radio button is checked, add a blue background */
.label-container input:checked ~ .radio-checkmark {
    background-color: #339af0;
    border-color: #339af0;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.radio-checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the indicator (dot/circle) when checked */
.label-container input:checked ~ .radio-checkmark:after {
    display: block;
}

/* Style the indicator (dot/circle) */
.label-container .radio-checkmark:after {
    top: 50%;
    left: 50%;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: #fff;
    transform: translate(-50%, -50%);
}

.label-container input:checked ~ .label {
    color: $black;
    font-weight: 500;
}
