.menu {
    display: flex;
    align-items: center;
    list-style: none;
    padding: 0;
    margin-bottom: 50px;
}

.menu__element {
    margin-right: auto;

    &:last-child {
        margin-right: 0;
    }
}

.menu__link {
    color: $black;
    font-size: 14px;
    font-weight: 500;
    transition-duration: 300ms;
    transition-property: color;

    &:hover {
        color: #ef8767;
        text-decoration: none;
    }

    .fa-bars {
        font-size: 15px;
        margin-right: 5px;
    }

    .fa-shopping-cart {
        color: #8a8c90;
        font-size: 17px;
        transition-duration: 300ms;
        transition-property: color;

        &:hover {
            color: #ef8767;
        }
    }
}

.search-form {
    position: relative;

    .fa-search {
        position: absolute;
        font-size: 15px;
        color: #ef8767;
        left: 25px;
        top: 13px;
    }
}

.menu__search {
    width: 334px;
    background-color: #f6f6f6;
    border: 0;
    border-radius: 20px;
    padding: 12px 50px;
    color: #8a8c90;

    &:focus {
        outline: none;
    }

    &::placeholder {
        color: #8a8c90;
        font-size: 13px;
        font-weight: 500;
    }
}

.cart-header {
    position: relative;

    span {
        display: inline-block;
        color: $white;
        background-color: #ef8767;
        text-align: center;
        font-size: 12px;
        padding: 2px 6px;
        border-radius: 50%;
        position: absolute;
        right: -10px;
        top: -10px;
    }
}

.checkout-header__wrap {
    padding-top: 20px;
    padding-bottom: 30px;
    display: flex;
    align-items: center;

    a {
        color: $black;
    }

    div {
        margin-left: auto;
        text-align: right;

        a {
            font-size: 13px;
            font-weight: 600;
            text-decoration: none;
            color: #339af0;
            transition-duration: 300ms;
            transition-property: color;

            &:hover {
                color: darken(#339af0, 10%);
            }
        }

        p {
            font-size: 15px;
            font-weight: 700;
            margin-top: 5px;
            margin-bottom: 0;
        }
    }
}

#menu-desktop {
    text-decoration: none;
    font-weight: 700;

    .fa-bars {
        color: #ef8767;
    }
}

.logo {
    font-size: 24px;
    text-decoration: none;
    font-weight: 700;
    line-height: 1.6;
    font-style: italic;
}

@media (max-width: 1150px) {
    .menu__search {
        width: 250px;
    }
}

@media (max-width: 1040px) {
    .menu__search {
        width: 200px;
    }
}

@media (max-width: 992px) {
    aside.desktop {
        display: none;
    }

    .menu {
        display: none;
    }
}
