.tabs {
    margin-bottom: 100px;
}

.tabcontent {
    display: none;

    &.active {
        display: block;
    }
}

.product__tabs {
    display: flex;
    margin-bottom: 35px;
    background-color: #f6f6f6;

    .tablinks {
        font-size: 14px;
        font-weight: 600;
        margin: 0;
        padding: 18px 43px;
        cursor: pointer;
        transition-duration: 300ms;
        transition-property: background-color;
        color: #8a8c90;
        background-color: #f6f6f6;
        border-right: 1px solid $white;

        &:hover {
            background-color: darken(#f6f6f6, 5%);
        }

        &.active {
            color: $black;
            border-bottom: 2px solid #ef8767;
        }
    }
}

.reviews__tab-section {
    display: flex;
}

.reviews__tab-left {
    margin-right: 110px;

    h4 {
        font-weight: 600;
        margin-bottom: 10px;
    }

    span {
        font-size: 14px;
        color: #000;
        font-weight: 600;
        padding-right: 8px;
    }

    .review__tab-btn {
        font-size: 12px;
        display: inline-block;
        text-decoration: none;
        background-color: #ef8767;
        border-radius: 4px;
        padding: 10px 11px;
        color: #fff;
        margin-top: 23px;
        transition-duration: 300ms;
        transition-property: background-color;

        &:hover {
            background-color: darken(#ef8767, 10%);
        }
    }
}

.reviews__tab-right {
    width: 70%;

    p {
        color: #8a8c90;
        font-size: 12px;
        font-weight: 500;
        line-height: 1.6;
        margin-bottom: 20px;

        a {
            color: #8a8c90;
            text-decoration: none;
            margin-left: 2px;
            margin-right: 2px;
            transition-duration: 300ms;
            transition-property: color;

            &:hover {
                color: $black;
            }
        }
    }

    .right__top {
        display: flex;

        p {
            color: $black;
            font-weight: 600;
            margin-bottom: 10px;

            &:last-child {
                margin-left: auto;
            }

            .fa-star {
                color: #f4cf62;
                margin-bottom: 1px;
                margin-left: 2px;
            }
        }
    }

    .right__bottom {
        display: flex;

        p {
            margin-bottom: 10px;

            &:last-child {
                margin-left: auto;
            }

            .fa-heart {
                cursor: pointer;
                transition-duration: 300ms;
                transition-property: color;

                &:hover {
                    color: #f13737;
                }
            }
        }
    }
}

.review__tab-block {
    border-bottom: 1px solid #f6f6f6;
    margin-bottom: 20px;
}

.review__tab-comment {
    max-width: 445px;
    margin-top: 35px;

    div {
        display: flex;
    }

    h2 {
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 16px;
    }

    .rating__block {
        color: #b7b9be;
        font-size: 17px;
        margin-left: auto;
    }

    .rating--color .fa-star {
        margin-right: 6.5px;
    }

    .review__comment-btn {
        font-size: 12px;
        display: inline-block;
        text-decoration: none;
        background-color: #b7b9be;
        border-radius: 4px;
        padding: 10px 20px;
        color: #fff;
        margin-top: 23px;
        transition-duration: 300ms;
        transition-property: background-color;

        &:hover {
            background-color: darken(#b7b9be, 5%);
        }
    }

    .comment__box {
        display: block;
        background-color: #f6f6f6;
        border: 1px solid #f6f6f6;
        border-radius: 5px;
        width: 100%;
        min-height: 140px;
        resize: none;
    }
}

@media (max-width: 767px) {
    .reviews__tab-left {
        margin-right: 50px;
    }

    .reviews__tab-right {
        width: 100%;
    }
}

@media (max-width: 590px) {
    .tabs {
        margin-bottom: 60px;
    }

    .reviews__tab-left {
        margin-right: 0;
        margin-bottom: 25px;
    }

    .reviews__tab-section {
        display: block;
    }
}

@media (max-width: 436px) {
    .tabs__title-hide + .wrap {
        padding-left: 0;
        padding-right: 0;
    }

    .product__tabs .tablinks {
        font-size: 13px;
        padding: 18px 20px;
    }
}

@media (max-width: 360px) {
    .review__tab-comment h2 {
        font-size: 16px;
    }

    .reviews__tab-left .review__tab-btn {
        float: right;
        margin-top: -20px;
    }

    .reviews__tab-left h4 {
        margin-bottom: 15px;
    }

    .reviews__tab-left {
        margin-right: 0;
        margin-bottom: 35px;

        .rating__block {
            margin-bottom: -3px;
        }
    }
}
