.check {
    line-height: 1.4;
}

.label-check {
    display: block;
    position: relative;
    padding-left: 22px;
    margin-bottom: 0;
    cursor: pointer;
    max-width: 320px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.label-check input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    position: absolute;
    top: 0;
    left: -5px;
    height: 18px;
    width: 18px;
    border-radius: 4px;
    background-color: #eee;
}

.label-check:hover input ~ .checkmark {
    background-color: #ccc;
}

.label-check input:checked ~ .checkmark {
    background-color: #339af0;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.label-check input:checked ~ .checkmark:after {
    display: block;
}

.label-check .checkmark:after {
    left: 5px;
    top: 2px;
    width: 8px;
    height: 11px;
    border: solid $white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

