.checkout-footer__wrap {
    padding-top: 25px;
    border-top: 4px solid #f6f6f6;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    p {
        font-size: 13px;
        color: #8a8c90;
        max-width: 435px;
        line-height: 1.8;
        margin-bottom: 25px;
        margin-right: 44px;

        a {
            color: #339af0;
        }
    }

    a {
        font-size: 13px;
        color: #8a8c90;
        text-decoration: none;
        margin-bottom: 25px;
        margin-right: 44px;

        &:nth-child(2) {
            font-size: 14px;
            color: $black;
            font-weight: 500;
        }

        &:nth-child(3) {
            margin-top: -15px;
        }

        &:nth-child(4) {
            margin-top: -15px;
            margin-right: 0;
        }

        &:hover {
            text-decoration: underline;
        }

        span {
            font-size: 13px;
            display: block;
            color: #8a8c90;
            margin-top: 3px;
        }
    }

    .fa-mobile,
    .fa-envelope,
    .fa-question-circle {
        font-size: 15px;
        margin-right: 3px;
        color: #339af0;
    }
}
