.breadcrumbs {
    background-color: #f6f6f6;
    margin-bottom: 40px;

    span {
        font-size: 13px;
        font-weight: 500;
    }

    a {
        display: inline-block;
        margin-bottom: 5px;
        color: #8a8c90;
        text-decoration: none;
        transition-duration: 300ms;
        transition-property: color;

        &:hover {
            color: $black;
        }
    }

    .fa-home {
        font-size: 13px;
        margin-right: 5px;
    }

    .fa-angle-double-right {
        font-size: 13px;
        color: #8a8c90;
        margin: 0 5px;
    }
}

.breadcrumbs__wrap {
    padding-top: 13px;
    padding-bottom: 8px;
    display: flex;
    align-items: center;
}

.checkout-breadcrumbs {
    display: flex;
    margin-bottom: 80px;

    a {
        text-decoration: underline;
    }

    .breadcrumbs-back {
        text-decoration: underline;
    }

    .breadcrumbs-center {
        font-weight: 700;
        margin-left: auto;
        margin-right: auto;
        transform: translateY(-2px);

        .fa-expeditedssl {
            font-size: 15px;
            color: #2ecc71;
        }
    }
}

@media (max-width: 425px) {
    .checkout-breadcrumbs {
        margin-bottom: 60px;

        span {
            font-size: 12px;
        }
    }
}
