.details {
    margin-bottom: 40px;
}

.product__details {
    display: flex;
}

.details__left {
    min-width: 260px;
    margin-right: 50px;
}

.details__right {
    .right__top {
        display: flex;

        div {
            .red-span {
                font-size: 14px;
                border-radius: 4px;
                padding: 0 8px;
                display: inline-block;
                background: #f13737;
                color: #fff;
                margin-bottom: 8px;
                font-weight: 500;
            }
        }

        .rating__block {
            margin-left: auto;

            span {
                padding: 0 8px;
            }
        }
    }

    p {
        color: #8a8c90;
        font-size: 12px;
        font-weight: 500;
        line-height: 1.6;
        margin-bottom: 20px;
    }

    .right__bottom {
        p {
            margin-bottom: 2px;

            .fa-check {
                color: #2ecc71;
                margin-right: 3px;
            }
        }
    }
}

@media (max-width: 800px) {
    .product__details {
        display: block;
    }

    .details__left {
        margin-right: 0;
        margin-bottom: 45px;
    }
}

@media (max-width: 575px) {
    .details__right {
        .right__top {
            flex-direction: column;

            div {
                h4 {
                    margin-bottom: 8px;
                }
            }

            .rating__block {
                margin-left: 0;
                margin-bottom: 15px;

                span {
                    padding-left: 0;
                }
            }
        }
    }
}
