aside.responsive {
    background-color: #f6f6f6;
}

.widgets {
    height: 100%;
    position: relative;
    padding-left: 30px;
    padding-right: 25px;

    &:first-child {
        padding-top: 25px;
    }

    &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 1px;
        height: 100%;
        display: block;
        background-color: #dbdfdf;
    }
}

.widgets__footer {
    padding-top: 25px;
    padding-bottom: 35px;
    padding-left: 30px;
    border-top: 40px solid $white;
    text-align: left;

    a {
        color: $black;
        font-size: 14px;
        font-weight: 500;
        transition-duration: 300ms;
        transition-property: color;

        &:hover {
            color: #ef8767;
            text-decoration: none;
        }
    }
}

.widgets__title {
    margin-top: 0;
    font-size: 14px;
    text-align: right;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.fa-sort-down {
    margin-left: auto;
    font-size: 20px;
    margin-bottom: 7px;
}

.widgets__block {
    margin-bottom: 30px;
}

.widgets__list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.widgets__item {
    text-align: left;

    a {
        line-height: 2;
        text-align: left;
        text-decoration: none;
        font-size: 14px;
        font-weight: 400;
        color: #8a8c90;
        text-decoration: none;
        transition-duration: 300ms;
        transition-property: color;

        &:hover {
            color: #ef8767;
        }
    }
}

.widgets__block-menu {
    display: inline-block;
    margin-top: 15px;
    padding: 25px 0;
    border-top: 1px solid #dbdfdf;
    padding-bottom: 0;
    margin-bottom: 0;
    padding-left: 90px;

    .menu {
        flex-direction: column;
        margin-right: 35px;

        .menu__element {
            margin-right: 0;
            margin-bottom: 15px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

@media (max-width: 990px) {
    .widgets {
        &::before {
            display: none;
        }

        &:first-child {
            padding-bottom: 15px;
        }
    }

    .widgets__blocks {
        padding-left: 0;
    }
}

@media (max-width: 678px) {
    aside.responsive {
        width: 60%;
    }
}

@media (max-width: 498px) {
    aside.responsive {
        width: 90%;
    }

    .widgets__block-menu {
        display: block;
    }
}
