.footer {
    padding: 40px 0 55px;
    border-top: 20px solid #f6f6f6;
    border-bottom: 1px solid #f6f6f6;
}

.footer__element {
    p {
        margin: 3px 0;
    }

    a {
        font-size: 13px;
        font-weight: 500;
        color: #8a8c90;
        line-height: 1.7;
        text-decoration: none;
        transition-duration: 300ms;
        transition-property: color;

        &:hover {
            color: $black;
        }
    }

    &:nth-child(2) {
        margin-top: 44px;
    }
}

.footer__title {
    font-size: 16px;
    color: $black;
    line-height: 1.5;
    font-weight: 600;
    margin-bottom: 23px;
    margin-top: 0;
}

.footer__element--options div {
    display: flex;
    align-items: center;
    margin-bottom: 25px;

    img {
        margin: 0 5px;

        &:first-child {
            margin-left: 0;
        }
    }
}

@media (min-width: 1200px) {
    .footer__element.footer__element--options {
        transform: translateX(-80px);
    }
}

@media (max-width: 1199px) {
    .footer__element--options div:first-child {
        flex-wrap: wrap;
    }
}

@media (max-width: 990px) {
    .footer__element.footer__element--options {
        transform: translateX(-30px);
    }
}

@media (max-width: 890px) {
    .footer__element.footer__element--options {
        transform: translateX(-50px);
    }

    .footer {
        padding: 30px 0 0;
    }

    .footer__element {
        margin-bottom: 23px;
    }
}

@media (max-width: 767px) {
    .footer__element.footer__element--options {
        transform: translateX(0);
    }
}

@media (max-width: 575px) {
    .footer__element {
        &:first-child {
            margin-bottom: 0;

            p:last-child {
                &::after {
                    content: "|";
                }
            }
        }

        &:nth-child(2) {
            margin-top: 0;
        }

        &.footer__element--options {
            margin-bottom: 0;
        }
    }

    .footer__element p {
        display: inline-block;

        &::after {
            content: "|";
            color: #8a8c90;
            margin-left: 6px;
        }

        &:last-child {
            &::after {
                content: "";
            }
        }
    }
}

@media (max-width: 390px) {
    .footer__element.footer__element--options {
        width: 40%;
    }
}
