.categories {
    margin-bottom: 20px;
}

.categories__top {
    display: flex;
}

.categories__title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 25px;
}

.categories__top .categories__more {
    font-size: 13px;
    display: inline-block;
    color: #ef8767;
    text-decoration: underline;
    line-height: 1.9;
    margin-left: auto;
    margin-right: 15px;
    font-weight: 500;
    transition-duration: 300ms;
    transition-property: color;

    &:hover {
        color: darken(#ef8767, 20%);
    }
}

.category__item {
    background-color: #f6f6f6;
    min-height: 180px;
    max-width: 130px;
    min-width: 125px;
    padding: 0 10px;
    margin-bottom: 20px;
    text-align: center;
    transition-duration: 300ms;
    transition-property: box-shadow;

    img {
        margin-top: 45px;
        margin-bottom: 37px;
    }

    .category__name {
        font-size: 13px;
        font-weight: 600;
        margin-bottom: 0;
    }
}

.categories__row--single {
    .category__item {
        max-width: 125px;
    }
}

.category__item--special {
    max-width: 560px;
    text-align: left;
    padding: 0 45px;
    padding-right: 65px;

    img {
        margin-bottom: 5px;
    }

    .category__name {
        display: inline-block;
        font-size: 24px;
        margin-top: 0;
        margin-left: 5px;
        margin-bottom: 0;
        font-weight: 700;
        transform: translateY(-22px);
    }

    .category__text {
        color: #8a8c90;
    }
}

.categories a {
    color: $black;
    text-decoration: none;
    font-size: 15px;
    font-weight: 500;
}

.categories__row {
    display: flex;
    flex-wrap: wrap;

    a {
        padding-right: 20px;

        &:hover {
            .category__item {
                box-shadow: 0 14px 22px 0 rgba(0, 0, 0, 0.12);
            }
        }
    }
}

.categories__row--single {
    a {
        padding-right: 23px;

        &:last-child {
            padding-right: 0;
        }
    }
}

@media (max-width: 1199px) {
    .category__item {
        max-width: 125px;
    }

    .category__item--special {
        max-width: 560px;
    }
}

@media (max-width: 1197px) {
    .categories__row--single {
        a {
            padding-right: 20px;
        }
    }
}

@media (max-width: 1169px) {
    .categories__row--single {
        a:last-child {
            padding-right: 20px;
        }
    }
}

@media (max-width: 899px) {
    .category__item--special {
        max-width: 415px;

        img {
            margin-top: 25px;
        }
    }
}

@media (max-width: 729px) {
    .categories__row {
        justify-content: flex-start;
    }
}

@media (max-width: 609px) {
    .categories__row {
        justify-content: center;
    }

    .categories__row--single {
        justify-content: flex-start;
    }
}

@media (max-width: 464px) {
    .categories__row--single {
        justify-content: center;
    }

    .categories__row a:last-child {
        padding-right: 0;
    }

    .categories__row--single a:last-child {
        padding-right: 20px;
    }
}

@media (max-width: 450px) {
    .category__item--special {
        padding: 0 5px 0 20px;

        .category__name {
            font-size: 21px;
        }
    }
}

@media (max-width: 350px) {
    .categories__row {
        a:nth-child(even) {
            padding-right: 0;
        }
    }

    .category__item--special {
        padding: 0 5px 0 10px;
    }
}
