.home__reviews {
    margin-bottom: 35px;
}

.home__review {
    margin-bottom: 20px;

    h4 {
        font-weight: 600;
        margin-bottom: 10px;
    }

    p {
        color: #8a8c90;
        font-size: 13px;
        font-weight: 500;
        line-height: 1.6;
        margin-bottom: 10px;
    }

    span {
        font-size: 14px;
        color: #000;
        font-weight: 500;
        padding-right: 8px;
    }
}

.home__review-btn {
    font-size: 12px;
    display: inline-block;
    background-color: #ef8767;
    border-radius: 4px;
    padding: 10px 11px;
    margin-top: 23px;
    color: #fff;
    text-decoration: none;
    transition-duration: 300ms;
    transition-property: background-color;

    &:hover {
        background-color: darken(#ef8767, 10%);
    }
}

.home__review-top {
    display: flex;

    p {
        color: $black;
        font-weight: 600;
        margin-bottom: 5px;

        &:last-child {
            margin-left: auto;
        }

        .fa-star {
            color: #f4cf62;
            margin-bottom: 1px;
            margin-left: 2px;
        }
    }
}
