.offers__wrap {
    margin-bottom: 50px;
}

.offers__title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 22px;
}

.offer__item a {
    color: $black;
    text-decoration: none;
    font-size: 15px;
    font-weight: 500;

    &:hover {
        .offer__name {
            color: #ef8767;
        }
    }
}

.offer__name {
    margin-bottom: 7px;
    font-weight: 600;
    margin-left: 6px;
    transition-duration: 300ms;
    transition-property: color;
}

.offer__text {
    font-size: 12px;
    font-weight: 500;
    margin-left: 6px;

    span {
        color: #f13737;
    }
}

.offer__bonus {
    margin-bottom: 8px;
    max-width: 260px;

    span {
        display: inline-block;
        margin-left: 6px;
        font-size: 14px;
        color: $white;
        background: #f13737;
        border-radius: 4px;
        padding: 0 8px;
    }
}

.offer__bg {
    min-height: 150px;
    max-width: 260px;
    margin-bottom: 10px;
    position: relative;

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &.green {
        background: #daeccd;
    }

    &.blue {
        background: #d9ecee;
    }

    &.brown-dark {
        background: #e4e1d4;
    }

    &.pink {
        background: #f6e8ed;
    }
}

@media (max-width: 767px) {
    .offer__item {
        margin-bottom: 20px;
    }

    .offers__wrap {
        margin-bottom: 20px;
    }
}

@media (max-width: 575px) {
    .offer__item {
        text-align: left;
    }

    .offer__bg {
        margin: 0 auto 10px;
    }

    .offer__name,
    .offer__text {
        max-width: 248px;
        margin-left: auto;
        margin-right: auto;
    }

    .offer__bonus {
        margin: 0 auto 8px;
        max-width: 254px;
    }
}
