.product-select__wrap {
    padding: 20px 100px;
    padding-left: 70px;
    padding-bottom: 25px;
    background-color: #f6f6f6;
    margin-bottom: 45px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    & > * {
        margin-right: auto;
    }

    div {
        span {
            display: inline-block;
            color: #8a8c90;
            font-size: 13px;
            font-weight: 400;
            margin-bottom: 5px;
        }
    }

    h2 {
        margin-bottom: 0;
        font-weight: 400;
        margin-top: 20px;
        margin-right: 45px;

        span {
            font-size: 19px;
        }
    }

    h4 {
        margin-bottom: 0;
        margin-top: 20px;
    }

    .product-select__btn {
        margin-right: 0;
        font-size: 13px;
        display: inline-block;
        background-color: #ef8767;
        border-radius: 8px;
        padding: 12px 32px;
        margin-top: 14px;
        color: #fff;
        text-decoration: none;
        transition-duration: 300ms;
        transition-property: background-color;

        &:hover {
            background-color: darken(#ef8767, 10%);
        }
    }

    .product-select__dropdown {
        background-color: $white;
        width: 280px;
        height: 40px;
        border-radius: 10px;
        border: 1px solid #d6dae3;
        position: relative;

        .fa-angle-down {
            color: #8a8c90;
            position: absolute;
            right: 15px;
            top: 50%;
            transform: translateY(-50%);
        }

        .custom-select {
            min-height: 40px;
            border: 0;
            width: 100%;
            padding-right: 7px;
        }

        .select-selected {
            color: $black;
            font-size: 14px;
            font-weight: 500;
            margin-left: 10px;
            padding: 10px 15px;
            border: 0;
            padding-left: 5px;

            &::after {
                display: none;
            }
        }
    }
}

.product-select--last {
    display: flex;
    align-items: center;
    margin-right: 0;
}

@media (max-width: 1140px) {
    .product-select__wrap {
        padding-left: 25px;
        padding-right: 25px;
    }
}

@media (max-width: 1040px) {
    h2.product-select__price {
        margin-right: 20px;
    }
}

@media (max-width: 822px) {
    h2.product-select__price {
        margin-right: 15px;
    }
}

@media (max-width: 750px) {
    .product-select__wrap {
        display: block;
        padding-bottom: 25px;

        h4 {
            text-align: center;
            margin-bottom: 15px;
            margin-top: 0;
        }

        h2 {
            display: inline-block;
            text-align: right;
        }

        div {
            margin-bottom: 10px;
        }
    }
}

@media (max-width: 320px) {
    .product-select__wrap {
        padding-left: 15px;
        padding-right: 28px;

        h2 {
            margin-right: 15px;
        }
    }

    .product-select--last {
        justify-content: flex-end;
    }
}
