@charset "UTF-8";

/**
 * General styles
 **/

html {
    font-size: 16px;
    color: $black;
}

body {
    font-family: "Montserrat", sans-serif;
    overflow-wrap: break-word;
    word-wrap: break-word;
}

img {
    max-width: 100%;
    height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin-top: 0;
}

.container {
    @include min-max(320px, 100%, 0);
}

.wrap {
    @include min-max(320px, 1200px, 15px);
}
