.products__wrap {
    margin-bottom: 50px;
}

.products__title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 22px;
}

.product__item a {
    color: $black;
    text-decoration: none;
    font-size: 15px;
}

.product__item:hover {
    .product__buy,
    .product__btn {
        opacity: 1;
        z-index: 1;
    }
}

.product__name {
    font-weight: 600;
    margin-left: 6px;
    transition-duration: 300ms;
    transition-property: color;
}

.product__text {
    font-size: 13px;
    color: #8a8c90;
    font-weight: 500;
    line-height: 1;
    margin-left: 6px;
}

.product__rating {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    margin-left: 6px;
    margin-right: 6px;
    max-width: 260px;

    span {
        font-size: 14px;
        border-radius: 4px;
        padding: 0 8px;
        display: inline-block;
        background: #f13737;
        color: $white;
        font-weight: 500;
    }
}

.product__bg {
    min-height: 150px;
    max-width: 260px;
    margin-bottom: 10px;
    position: relative;

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &.green {
        background: #daeccd;
    }

    &.blue {
        background: #d9ecee;
    }

    &.brown {
        background: #f9f2dd;
    }

    &.pink-dark {
        background: #eadfe4;
    }
}

.product__buy {
    display: block;
    background-color: #f3f7f7;
    padding: 14px 0;
    text-align: center;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    z-index: -1;
    transition-duration: 500ms;
    transition-property: opacity;

    .product__btn {
        font-size: 14px;
        line-height: 1;
        border-radius: 4px;
        padding: 8px 26px;
        display: inline-block;
        background: #ef8767;
        color: #fff;
        transition-duration: 500ms;
        opacity: 0;
        z-index: -1;
        transition-property: opacity, background-color;

        &:hover {
            background-color: darken(#ef8767, 10%);
        }
    }
}

@media (max-width: 991px) {
    .product__item {
        margin-bottom: 15px;
    }

    .products__wrap {
        margin-bottom: 20px;
    }

    .product__name,
    .product__text {
        max-width: 260px;
    }
}

@media (max-width: 767px) {
    .product__item {
        margin-bottom: 20px;
    }

    .products__wrap {
        margin-bottom: 10px;
    }
}

@media (max-width: 575px) {
    .product__item {
        text-align: left;
    }

    .product__bg {
        margin: 0 auto 10px;
    }

    .product__rating {
        margin: 0 auto 8px;
        max-width: 248px;
    }

    .product__name,
    .product__text {
        max-width: 248px;
        margin-left: auto;
        margin-right: auto;
    }
}
