section.payment {
    margin-bottom: 140px;
}

.recepient,
.shipping,
.payment__card,
.total__block {
    max-width: 350px;
}

.recepient__form-title,
.shipping__form-title,
.payment__form-title,
.total__form-title {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 12px;
}

.total__block {
    border: 1px solid #d6dae3;
    margin-bottom: 20px;
}

.total__top {
    padding: 0 20px 0;

    & > div {
        display: flex;
        align-items: center;
        padding: 20px 0 20px;

        &:first-child {
            border-bottom: 1px solid #d6dae3;
        }
    }

    p {
        font-size: 14px;
        margin-bottom: 2px;
        font-weight: 500;

        &:last-child {
            margin-top: -16px;
            margin-left: auto;
        }
    }

    span {
        font-size: 12px;
        color: #8a8c90;
    }

    img {
        margin-right: 10px;
    }
}

.total__bottom {
    border-top: 1px solid #d6dae3;

    & > div {
        background-color: #f6f6f6;
        display: flex;
        align-items: center;

        &:first-child {
            border-bottom: 1px solid $white;
            padding: 20px 13px;
        }

        &:last-child {
            border-bottom: 1px solid $white;
            padding: 20px 13px 30px;
        }
    }

    p {
        font-size: 14px;
        font-weight: 500;
        margin-bottom: 0;

        &.total-grey {
            color: #8a8c90;
        }

        &.total-amount {
            font-size: 16px;
        }

        &:last-child {
            margin-left: auto;
            font-weight: 700;
        }
    }
}

.total__btn {
    text-decoration: none;
    font-weight: 500;
    color: $white;
    background-color: #2ecc71;
    display: inline-block;
    width: 100%;
    max-width: 350px;
    text-align: center;
    border-radius: 5px;
    padding: 18px 0;
    transition-duration: 300ms;
    transition-property: background-color;

    &:hover {
        background-color: darken(#2ecc71, 5%);
    }
}

.custom-select {
    border: 1px solid #d6dae3;
    width: 100%;
    padding-right: 30px;
}

.recepient__form {
    form {
        select {
            position: relative;

            & ~ .valid-border {
                content: "";
                display: none;
                height: 38px;
                width: 8px;
                background-color: #ff5d5d;
                position: absolute;
                left: -1.2%;
                top: 0;
                z-index: 1;
            }

            &:valid {
                & ~ .valid-border {
                    background-color: #83c550;
                    display: block;
                }
            }
        }
    }
}

.recepient__form-bottom {
    border: 1px solid #d6dae3;
    border-top: 0;
    padding: 12px 20px 30px;

    p {
        font-size: 12px;
        font-weight: 500;
        color: #8a8c90;
        margin-bottom: 25px;

        span {
            color: #ff0000;
        }
    }

    .check {
        font-size: 13px;
        font-weight: 500;
    }
}

.shipping__form {
    border: 1px solid #d6dae3;
    padding: 20px 15px 16px;
    margin-bottom: 16px;
}

.form-control {
    display: block;
    border: 1px solid #d6dae3;
    border-bottom: 0;
    background-color: $white;
    padding: 13px 15px;
    width: 100%;
    position: relative;

    & ~ .valid-border {
        content: "";
        display: none;
        height: 38px;
        width: 8px;
        background-color: #ff5d5d;
        position: absolute;
        left: 0;
        top: 15%;
        z-index: 1;
    }

    &::placeholder {
        font-size: 14px;
        color: #8a8c90;
        font-weight: 400;
        line-height: 1.2;
        opacity: 0;
    }

    &:focus {
        outline: none;
    }

    &:invalid:not(:placeholder-shown) {
        & ~ .fa-exclamation {
            display: block;
        }

        & ~ .valid-border {
            display: block;
        }

        form div::before {
            display: block;
        }

        & {
            color: #ff0000;
        }
    }

    &:required:valid {
        & + .fa-check {
            display: block;
        }

        & ~ .valid-border {
            background-color: #83c550;
            display: block;
        }
    }
}

form div {
    position: relative;

    .fa-exclamation {
        position: absolute;
        right: 4%;
        top: 33%;
        color: #ff0000;
        display: none;
    }

    .fa-check {
        position: absolute;
        right: 3%;
        top: 33%;
        color: #2ecc71;
        display: none;

        &.active {
            display: block;
        }

        &.none {
            display: none;
        }
    }
}

.zip-city {
    display: flex;

    div {
        width: 50%;
    }
}

.zip-form {
    border-right: 0;
}

.phone-form {
    border-top: 0;
    border-bottom: 1px solid #d6dae3;
}

.contact__form-title {
    font-size: 18px;
    color: #515a5e;
    font-weight: 700;
    line-height: 1.6;
}

.credit-card {
    margin-bottom: 70px;
}

.payment__options {
    width: 60%;
    border: 8px solid #f2f6f8;
    display: flex;
    padding: 15px 15px 10px;
    margin-bottom: 20px;
}

.companies__icons {
    margin-left: auto;
}

.card__info {
    display: flex;
    flex-wrap: wrap;
}

.card__info-form {
    border: 1px solid #d6dae3;
}

.card__info-top {
    padding: 14px;
    padding-bottom: 12px;
}

.card-form-control {
    display: block;
    border: 1px solid #d6dae3;
    background-color: $white;
    padding: 15px;
    width: 100%;
    position: relative;

    &::placeholder {
        font-size: 14px;
        color: #8a8c90;
        font-weight: 400;
        line-height: 1.2;
    }

    &:focus {
        outline: none;
    }

    &.cc-expires {
        display: inline-block;
        width: 50%;
        border-left: 0;
        border-right: 0;

        &:nth-child(1) {
            border-left: 1px solid #d6dae3;
        }

        &:nth-child(2) {
            float: left;
        }
    }

    &.cc-number {
        border-left: 0;
        border-right: 0;
        border-bottom: 0;
    }

    &.cc-cvv {
        border: 0;
    }
}

.card__info-cvv {
    color: #333;
    margin-left: 20px;
    margin-top: 18px;

    h3 {
        margin-bottom: 5px;
        font-size: 14px;
    }

    p {
        margin-bottom: 5px;
        font-size: 12px;
    }
}

.cc-number {
    padding-left: 47px;

    & + span {
        width: 20px;
        height: 15px;
        position: absolute;
        top: 15px;
        left: 17px;
    }
}

.cc-cvv {
    & + .fa-question-circle {
        cursor: pointer;
        color: #cad1d8;
        position: absolute;
        top: 17px;
        right: 17px;
        transition-property: color;
        transition-duration: 300ms;

        &:hover {
            color: #515a5e;
        }
    }
}

.card__info-top div {
    display: flex;

    &:first-child {
        margin-bottom: 2px;
    }

    .label-container {
        padding-left: 28px;

        &:first-child {
            margin-right: 15px;
        }
    }
}

div.card__options {
    display: block;
    font-size: 35px;

    span {
        color: #9aa4b0;
        cursor: pointer;
        transition-duration: 300ms;
        transition-property: color;

        &.active {
            color: #339af0;
        }

        &:hover {
            color: #339af0;
        }
    }
}

.card-icon-sm {
    font-size: 19px;
    color: #339af0;
}

input:focus ~ .floating-label,
input:not(:focus):valid ~ .floating-label {
    top: 2px;
    bottom: 10px;
    left: 45px;
    font-size: 11px;
    opacity: 1;
}

.floating-label {
    font-size: 14px;
    color: #8a8c90;
    font-weight: 400;
    position: absolute;
    pointer-events: none;
    left: 48px;
    top: 17px;
    transition: 0.2s ease all;
}

.floating-label-recepient {
    font-size: 14px;
    color: #8a8c90;
    font-weight: 400;
    position: absolute;
    pointer-events: none;
    left: 16px;
    top: 15px;
    transition: 0.2s ease all;
    z-index: 1;

    span {
        color: #ff0000;
    }
}

input:focus ~ .floating-label-recepient,
input:not(:focus):valid ~ .floating-label-recepient,
input:invalid:not(:placeholder-shown) ~ .floating-label-recepient {
    opacity: 0;
}

.floating-country {
    left: 75px;
    top: 14px;
}

@media (max-width: 1199px) {
    .recepient,
    .shipping,
    .payment__card,
    .total__block {
        margin-bottom: 25px;
    }

    .shipping {
        margin-bottom: 0;
    }
}

@media (max-width: 767px) {
    .recepient,
    .shipping,
    .payment__card,
    .total__block,
    .total__btn {
        max-width: none;
    }
}
