.desktop-menu {
    display: none;
}

aside.desktop {
    position: relative;
    background-color: #f6f6f6;
    padding: 35px;
    border-bottom: 2px solid #ef8767;
    display: block;
    position: absolute;
    top: 68px;
    left: 0;
    right: 0;
    z-index: 1;

    h3 {
        font-size: 14px;
    }

    p {
        margin-bottom: 6px;

        & + h3 {
            margin-top: 25px;
        }
    }

    a {
        font-size: 13px;
        font-weight: 500;
        color: #8a8c90;
        text-decoration: none;
        transition-duration: 300ms;
        transition-property: color;

        &:hover {
            color: #ef8767;
        }
    }

    &::before {
        content: "";
        border: 11px solid transparent;
        border-left: 14px solid white;
        position: absolute;
        left: 7.5%;
        top: 0%;
        transform: rotate(90deg);
    }
}

@media (max-width: 1040px) {
    aside.desktop::before {
        left: 8%;
    }
}
