.mobile__menu {
    display: none;
    padding-top: 10px;
    padding-bottom: 10px;

    a {
        color: $black;

        .fa-bars {
            font-size: 20px;
            color: #ef8767;
        }
    }

    .logo {
        font-size: 26px;
        margin-left: auto;
        margin-right: auto;
    }

    .fa-shopping-cart {
        font-size: 20px;
        color: #8a8c90;
        transition-duration: 300ms;
        transition-property: color;

        &:hover {
            color: #ef8767;
        }
    }
}

aside.mobile-menu {
    display: none;
}

@media (max-width: 992px) {
    .mobile__menu {
        display: flex;
        align-items: center;
    }
}

@media (max-width: 992px) {
    aside.responsive {
        display: block;
        top: 61px;
        left: 0;
        position: absolute;
        width: 45%;
        background-color: #fff;
        text-align: right;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        z-index: 3;
    }

    .container.mobile {
        position: relative;

        &::after {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background-color: rgba(0, 0, 0, 0.7);
            z-index: 1;
        }
    }
}
