.home-slider__wrap {
    margin-bottom: 45px;
    box-sizing: border-box;
}

.slider {
    background: linear-gradient(to bottom, #914b6b 90%, white 0%);
    min-height: 365px;
}

.slider__title {
    color: $white;
    font-weight: 500;
    margin-bottom: 30px;
}
.slider__info {
    padding-top: 55px;
    padding-left: 75px;
    padding-right: 80px;
    display: flex;
    flex-wrap: wrap;
}

.slider__info-right {
    max-width: 340px;
    margin-left: auto;
}

.slider__info-left {
    max-width: 480px;

    p {
        color: $white;
        font-size: 12px;
        font-weight: 500;
        line-height: 1.6;
        margin-bottom: 20px;
    }
}

p.slider__text {
    font-size: 11px;
    margin-bottom: 0;
}

.slider__icons {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    margin-bottom: 5px;

    .circle {
        display: inline-block;
        border: 2px solid $white;
        border-radius: 50%;
        padding: 7.5px 4px;
        margin-bottom: 3px;
    }

    svg {
        fill: $white;
    }
}

.slider__icon {
    color: $white;
    text-align: center;

    &:nth-child(2) {
        margin-left: 35px;
        margin-right: 35px;
    }

    p {
        font-weight: 500;
    }
}

@media (max-width: 1069px) {
    .slider__info {
        padding-left: 45px;
        padding-right: 40px;
    }
}

@media (max-width: 990px) {
    .slider {
        background: linear-gradient(to bottom, #914b6b 77%, #fff 0%);
    }

    .slider__info {
        padding-top: 40px;
    }

    .slider__info-right {
        max-width: 300px;
    }

    .slider__icon p {
        margin-bottom: 3px;
    }
}

@media (max-width: 910px) {
    .slider__info {
        padding-left: 20px;
        padding-right: 10px;
    }
}

@media (max-width: 840px) {
    .slider__info-right {
        margin-left: 0;
    }

    .slider__info {
        flex-direction: column;
        align-items: center;
        padding-left: 10px;
    }

    .slider__icon p {
        margin-bottom: 10px;
    }

    .slider__info-left {
        margin-bottom: 25px;
    }

    .slider {
        background: linear-gradient(to bottom, #914b6b 91%, #fff 0%);
    }
}

@media (max-width: 768px) {
    .slider__info {
        padding-top: 50px;
    }
}

@media (max-width: 468px) {
    .slider__info {
        padding-left: 15px;
        padding-right: 15px;
    }

    .slider__text {
        display: none;
    }

    .slider__info-left {
        margin-bottom: 20px;
    }
}

@media (max-width: 426px) {
    .slider__icon:nth-child(2) {
        margin-left: 12px;
        margin-right: 12px;
    }

    .slider__icon p {
        font-size: 10px;
    }

    .slider__info-left {
        margin-bottom: 10px;
    }
}

@media (min-width: 841px) and (max-width: 990px) {
    .home-slider__wrap {
        margin-bottom: 0;
    }
}
