.owl-theme {
    // Styling Next and Prev buttons
    .owl-nav {
        margin-top: 10px;
        text-align: center;
        -webkit-tap-highlight-color: transparent;

        [class*="owl-"] {
            color: $owl-nav-color;
            font-size: $owl-nav-font-size;
            margin: $owl-nav-margin;
            padding: $owl-nav-padding;
            background: $owl-nav-background;
            display: inline-block;
            cursor: pointer;
            border-radius: 3px;

            &:hover {
                background: $owl-nav-background-hover;
                color: $owl-nav-color-hover;
                text-decoration: none;
            }
        }
        .disabled {
            opacity: $owl-nav-disabled-opacity;
            cursor: default;
        }
    }

    // Styling dots
    // .owl-nav.disabled + .owl-dots {
    //     margin-top: 10px;
    // }

    .owl-dots {
        text-align: center;
        -webkit-tap-highlight-color: transparent;

        .owl-dot {
            outline: none;
            display: inline-block;
            zoom: 1;
            *display: inline;

            span {
                width: $owl-dot-width;
                height: $owl-dot-height;
                margin: $owl-dot-margin;
                background: $owl-dot-background;
                display: block;
                -webkit-backface-visibility: visible;
                transition: opacity 200ms ease;
            }

            &.active,
            &:hover {
                span {
                    background: $owl-dot-background-active;
                }
            }
        }
    }

    .owl-dots {
        // margin-top: -15px;
        // margin-bottom: 10px;
        // text-align: right;

        .owl-dot {
            display: inline-block;
            zoom: 1;
            *display: inline;

            span {
                margin: 0 4px;
                margin-left: 0;
                margin-right: 13px;
                height: 10px;
                width: 10px;
                border-radius: 50%;
                background-color: #bd93a6;
                display: inline-block;
                cursor: pointer;
            }

            &:last-child {
                span {
                    &:last-child {
                        margin-right: 0;
                    }
                }
            }

            &.active,
            &:hover {
                span {
                    background: $white;
                }
            }

            &.active,
            &:focus {
                span {
                    outline: none;
                }
            }
        }
    }
}

.owl-theme .owl-nav [class*="owl-"] {
    margin: 0;
}

.owl-theme .owl-nav [class*="owl-"]:hover {
    background: transparent;
    color: transparent;
    text-decoration: none;
}

.owl-theme .owl-nav [class*="owl-"]:focus {
    outline: none;
}

.owl-theme .owl-nav .disabled {
    opacity: 1;
}

@media (max-width: 768px) {
    .owl-theme {
        .owl-dots {
            margin-bottom: 35px;
        }
    }
}
