.rating__block {
    display: flex;
    font-size: 13px;
    color: #758a8b;
    font-weight: 400;
    margin-left: auto;

    span {
        font-size: 13px;
        color: $black;
        font-weight: 600;
        background: none;
    }
}

.rating {
    position: relative;
    display: inline-block;

    .fa-star {
        margin-right: 2px;

        &:last-child {
            margin-right: 0;
        }
    }
}

.rating--color {
    position: absolute;
    display: flex;
    flex-wrap: nowrap;
    top: 0;
    left: 0;
    width: 0;
    overflow: hidden;

    .fa-star,
    .fa-star-half {
        color: #f4cf62;
        margin-right: 5.4px;

        &:last-child {
            margin-right: 0;
        }
    }
}
